import { useState, useMemo } from "react";
import { useForm } from "react-hook-form";
import { zodResolver } from "@hookform/resolvers/zod";
import * as z from "zod";
import { useTranslation } from "react-i18next";
import { Link } from "react-router-dom";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Checkbox } from "@/components/ui/checkbox";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Form, FormControl, FormDescription, FormField, FormItem, FormLabel, FormMessage } from "@/components/ui/form";
import { supabase } from "@/integrations/supabase/client";
import { toast } from "@/hooks/use-toast";
import { CheckCircle2, Loader2, Store, Phone, Mail } from "lucide-react";
import { useSiteSettings } from "@/hooks/useSiteSettings";

const featureOptions = ["booking", "delivery", "qrcode"] as const;

type FormValues = {
  name: string;
  email: string;
  phone: string;
  restaurant_name: string;
  restaurant_address: string;
  features_selected: typeof featureOptions[number][];
};

const JoinUs = () => {
  const { t, i18n } = useTranslation();
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [isSuccess, setIsSuccess] = useState(false);
  const { settings } = useSiteSettings();

  const formSchema = useMemo(() => z.object({
    name: z.string().min(2, { message: t("joinUs.nameMin") }),
    email: z.string().email({ message: t("joinUs.emailInvalid") }),
    phone: z.string().min(6, { message: t("joinUs.phoneMin") }),
    restaurant_name: z.string().min(2, { message: t("joinUs.restaurantNameMin") }),
    restaurant_address: z.string().min(2, { message: t("joinUs.addressMin") }),
    features_selected: z.array(z.enum(featureOptions)).min(1, { message: t("joinUs.featuresMin") }),
  }), [t]);

  const form = useForm<FormValues>({
    resolver: zodResolver(formSchema),
    defaultValues: {
      name: "",
      email: "",
      phone: "",
      restaurant_name: "",
      restaurant_address: "",
      features_selected: [],
    },
  });

  const onSubmit = async (data: FormValues) => {
    setIsSubmitting(true);
    try {
      // Get current language from i18n
      const currentLanguage = i18n.language || 'en';
      const { error } = await supabase.functions.invoke("send-join-request-email", {
        body: { ...data, language: currentLanguage },
      });

      if (error) {
        if (error.message.includes("rate_limit_exceeded")) {
          toast({
            title: t("joinUs.rateLimitError"),
            description: t("joinUs.rateLimitErrorDesc"),
            variant: "destructive",
          });
        } else {
          throw error;
        }
        return;
      }

      setIsSuccess(true);
      form.reset();
      toast({
        title: t("joinUs.successTitle"),
        description: t("joinUs.successDescription"),
      });
    } catch (error: any) {
      console.error("Error submitting join request:", error);
      toast({
        title: t("common.error"),
        description: t("joinUs.submitError"),
        variant: "destructive",
      });
    } finally {
      setIsSubmitting(false);
    }
  };

  // Get localized success content from settings
  const getSuccessTitle = () => {
    const lang = i18n.language;
    if (lang === 'zh' && settings?.join_success_title_zh) return settings.join_success_title_zh;
    if (lang === 'pt' && settings?.join_success_title_pt) return settings.join_success_title_pt;
    if (settings?.join_success_title_en) return settings.join_success_title_en;
    return t("joinUs.successTitle");
  };

  const getSuccessMessage = () => {
    const lang = i18n.language;
    if (lang === 'zh' && settings?.join_success_message_zh) return settings.join_success_message_zh;
    if (lang === 'pt' && settings?.join_success_message_pt) return settings.join_success_message_pt;
    if (settings?.join_success_message_en) return settings.join_success_message_en;
    return t("joinUs.successMessage");
  };

  const contactPhone = settings?.join_contact_phone || '968815050';
  const contactEmail = settings?.contact_email;

  if (isSuccess) {
    return (
      <div className="min-h-screen pt-20 pb-12 px-4 bg-secondary/20">
        <div className="container mx-auto max-w-2xl">
          <Card className="border-primary">
            <CardContent className="pt-12 pb-12 text-center">
              <CheckCircle2 className="w-16 h-16 text-primary mx-auto mb-4" />
              <h2 className="text-2xl font-bold text-foreground mb-4">
                {getSuccessTitle()}
              </h2>
              <p className="text-muted-foreground mb-4 max-w-md mx-auto">
                {getSuccessMessage()}
              </p>
              <div className="flex flex-col sm:flex-row items-center justify-center gap-4 mb-6">
                <a href={`tel:${contactPhone}`} className="flex items-center gap-2 text-primary font-semibold hover:underline">
                  <Phone className="w-5 h-5" />
                  <span>{contactPhone}</span>
                </a>
                {contactEmail && (
                  <a href={`mailto:${contactEmail}`} className="flex items-center gap-2 text-primary font-semibold hover:underline">
                    <Mail className="w-5 h-5" />
                    <span>{contactEmail}</span>
                  </a>
                )}
              </div>
              <Button onClick={() => setIsSuccess(false)}>
                {t("joinUs.submitAnother")}
              </Button>
            </CardContent>
          </Card>
        </div>
      </div>
    );
  }

  return (
    <div className="min-h-screen pt-4 pb-12 px-4 bg-secondary/20">
      <div className="container mx-auto max-w-3xl">
        <Card>
          <CardHeader className="text-center space-y-2">
            <div className="flex justify-center mb-4">
              <Store className="w-12 h-12 text-primary" />
            </div>
            <CardTitle className="text-3xl font-bold">{t("joinUs.title")}</CardTitle>
            <CardDescription className="text-base">
              {t("joinUs.description")}
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Form {...form}>
              <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                <FormField
                  control={form.control}
                  name="name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>{t("joinUs.name")}</FormLabel>
                      <FormControl>
                        <Input placeholder={t("joinUs.namePlaceholder")} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="email"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>{t("joinUs.email")}</FormLabel>
                      <FormControl>
                        <Input type="email" placeholder={t("joinUs.emailPlaceholder")} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="phone"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>{t("joinUs.phone")}</FormLabel>
                      <FormControl>
                        <Input type="tel" placeholder={t("joinUs.phonePlaceholder")} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="restaurant_name"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>{t("joinUs.restaurantName")}</FormLabel>
                      <FormControl>
                        <Input placeholder={t("joinUs.restaurantNamePlaceholder")} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="restaurant_address"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>{t("joinUs.restaurantAddress")}</FormLabel>
                      <FormControl>
                        <Input placeholder={t("joinUs.restaurantAddressPlaceholder")} {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <FormField
                  control={form.control}
                  name="features_selected"
                  render={() => (
                    <FormItem>
                      <div className="mb-4">
                        <FormLabel>{t("joinUs.featuresNeeded")}</FormLabel>
                        <FormDescription>
                          {t("joinUs.featuresDescription")}
                        </FormDescription>
                      </div>
                      {featureOptions.map((feature) => (
                        <FormField
                          key={feature}
                          control={form.control}
                          name="features_selected"
                          render={({ field }) => {
                            return (
                              <FormItem
                                key={feature}
                                className="flex flex-row items-start space-x-3 space-y-0"
                              >
                                <FormControl>
                                  <Checkbox
                                    checked={field.value?.includes(feature)}
                                    onCheckedChange={(checked) => {
                                      return checked
                                        ? field.onChange([...field.value, feature])
                                        : field.onChange(
                                            field.value?.filter((value) => value !== feature)
                                          );
                                    }}
                                  />
                                </FormControl>
                                <FormLabel className="font-normal cursor-pointer">
                                  {t(`joinUs.feature.${feature}`)}
                                </FormLabel>
                              </FormItem>
                            );
                          }}
                        />
                      ))}
                      <FormMessage />
                    </FormItem>
                  )}
                />

                <div className="text-xs text-muted-foreground text-center pb-4">
                  {t("joinUs.consentText")}{" "}
                  <Link to="/privacy-policy" className="text-primary hover:underline">
                    {t("joinUs.privacyPolicy")}
                  </Link>{" "}
                  {t("joinUs.and")}{" "}
                  <Link to="/terms-of-service" className="text-primary hover:underline">
                    {t("joinUs.termsOfService")}
                  </Link>
                </div>

                <Button type="submit" className="w-full" size="lg" disabled={isSubmitting}>
                  {isSubmitting && <Loader2 className="mr-2 h-4 w-4 animate-spin" />}
                  {t("joinUs.submit")}
                </Button>
              </form>
            </Form>
          </CardContent>
        </Card>
      </div>
    </div>
  );
};

export default JoinUs;
